const {abi} = require('@predmark/dapps.contracts/src/contracts/abi/Quiz.json'),
DelphiERC20 = require('./delphiERC20'),
    clientContract = require('./clientContracts');

class Quiz extends clientContract {
    constructor(options) {
        super({
            abi,
            contractAddress: options.network.quiz,
            network: options.network,
            BICONOMY_API_KEY: options.network.biconomy_api_key,
            instrument: options.instrument
        });
        this.account = options.account;
        this.isLive = options.isLive;
        
        if(!this.isLive) {
            this.delphiClient = new DelphiERC20(options);
        }
    }
    async init() {
        await super.init();
        if(this.delphiClient) {
            await this.delphiClient.init();
            return await this.delphiClient.signerContract.allowance(this.account, this.contractAddress);
        }
        
    }
    async approve(amount) {
        if(this.delphiClient) {
            const withSigner = this.delphiClient.signerContract.connect(this.delphiClient.metamaskProvider.getSigner());
            await withSigner.approve(this.contractAddress, amount, {gasLimit: 2000000});
        }
    }
    async lockGameTokens(){
        const withSigner = this.signerContract.connect(this.metamaskProvider.getSigner());
        const eventID = await this.getEventID();
        return await withSigner.lockGameTokens(eventID,{gasLimit: 1000000});
    }
    //'Daily_Music_Quiz', 3155760000, 0x02D99535E1BEFb3B31c37def6A87767773763aCF
    async getEventID(statement, endInSecond, creator){
        console.log(this.nonBiconomyContract);
        const eventID = await this.nonBiconomyContract.getEventID(statement, endInSecond, creator);
        return eventID;    
    }

    
}

module.exports = Quiz;