export default {
    encode: function(input){
        let encoded = "enc:"
        for (idx in input){
            const ascii = (input.charCodeAt(idx))
            const octal = parseInt(ascii, 10).toString(8);
            encoded += (input.charAt(idx), parseInt(octal, 10).toString(16));
        }
        return encoded;
    },
    decode: function(encoded){
        let decoded = "";
        if (encoded.indexOf("enc:") > -1){
            const toks = encoded.split(":");
            const input = toks[1];
            const len = input.length/2;
            for (let i=0; i<len; i++){
                const substr = input.substr(i*2, 2);
                const hex = parseInt(substr, 16);
                const oct = parseInt(hex, 8).toString(10);
                decoded += String.fromCharCode(oct);
            }
        }
        else {
            decoded = encoded;
        }
        return decoded;
    }
}
