<template>
<div>
    <div class="heading">
        <h4>The Most Unique<br>Daily Music Quiz Game</h4>
        <h2>Deposit 1 Delphi Token into the Game Contract to Unlock the Game</h2>
        <h2></h2>
    </div>
    <input type="hidden" id="testing-code" :value="shareText">
    <div id="app">
      <v-expansion-panels>
    <v-expansion-panel 
      v-for="(item,i) in 1"
      :key="i"
      style="max-width:374px;"
    >
      <v-expansion-panel-header>
        Rules
      </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-card cols="6" lg="6">
        <v-card-title>Guess the Song using</v-card-title>
        <v-card-title>Hint 1 (GOLD) - Audio Hint</v-card-title>
        <v-card-title>Hint 2 (SILVER) - Text/Audio</v-card-title>
        <v-card-title>Hint 3 (BRONZE) - Text/Audio</v-card-title>
        <v-card-text>Lucky Winners chosen from Gold, Silver, Bronze medalists take the Whole Pot End of Every Day.</v-card-text>
        <v-card-text>Part of the Pot Goes to Music Composers</v-card-text>
        
        </v-card>
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
        <v-card elevation="8" outlined tile class="mx-auto my-12"
    max-width="374" v-if="clip != undefined">
    <v-btn @click="deposit()" color="error" class="mb-1" :disabled="hintsRevealed[0]">Deposit to Unlock Game</v-btn>
    <v-btn icon @click="checkLockStatus()">
                    <v-icon color="success">mdi-refresh</v-icon>
                    
                </v-btn>
    <v-img
            src="/music_rainbow.jpeg"
            height="250"
          />
            <v-card-title>Quiz by {{getClipContributor()}}</v-card-title>
                <v-row v-for="(hint, index) in clip.hints" :key="index" v-show="hintsRevealed[index]">
                  <v-col v-if="clip.hints[index].audio" cols="12">
                      <audio controls >
                        <source :src="getClipURL(index)" type="audio/mpeg">
                        Your browser does not support the audio tag.
                      </audio>
                   </v-col>
                   <v-col v-if="clip.hints[index].img" cols="12">
                      <v-img :src="getImgURL(index)"></v-img>
                   </v-col>
                   <v-col v-if="clip.hints[index].text"  cols="12">
                     {{clip.hints[index].text}}
                   </v-col>
                   <v-col v-if="index < clip.hints.length-1"  cols="12">
                       <v-btn @click="setHintsRevealed(index+1)" :color="getHintColor(index + 1)">{{getHintText(index+1)}}</v-btn>
                   </v-col>
                </v-row>
                <v-row>
                    <v-col>
                      <v-card-text>
      
      <v-select
        :items="songList"
        color="white"
        label="Answer"
        v-model="answer"
      ></v-select>
    </v-card-text>
                        <v-btn @click="evaluate()" v-if="!latestPlayed && hintsRevealed[0]==true" color="accent" class="mb-1">Submit Answer</v-btn>
                        <span v-if="latestPlayed">Next Sargam Soon !</span>
                    </v-col>
                </v-row>
                <v-row v-if="latestPlayed">
                  <iframe width="560" height="315" :src="getEmbedUrl()" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                </v-row>
        </v-card>

        <v-dialog
          v-model="overlayShow"
        >
        <v-card class="triangle-card" max-width="374">
          <v-card-title class="page-title text-no-wrap pt-2">
              {{winMessage}} 🥳
            </v-card-title>
            <v-card-actions>
            <v-row>
              <v-col>
               <v-btn icon @click="phoneShare()">
                    <v-icon color="success">mdi-share</v-icon>
                    <div>SHARE</div>
                </v-btn>
          </v-col>
          <v-col>
            <v-btn
              icon
              @click="overlayShow = false"
            >
              <v-icon color="error">mdi-close-circle</v-icon>
              <div>CLOSE</div>
            </v-btn>
              </v-col>
          </v-row>
          </v-card-actions>
        </v-card>
        </v-dialog>
    </div>
    <v-snackbar v-model="snack.display" :timeout="snack.duration" :color="snack.color">
      {{ snack.text }}
      <v-btn text @click="snack.display = false">Close</v-btn>
    </v-snackbar> 
</div>
</template>
<script>
import Quiz from "../lib/contracts/quiz";
import BigNumber from 'bignumber.js';

import {mapGetters, mapMutations} from 'vuex';
import moment from "moment";
import {EventBus} from '../store/eventBus';

import utils from '../utils/utils'
export default{
    mounted: async function(){
        
        this.client = new Quiz({
            network: this.network,
            instrument: this.getSelectedInstrument,
            answerListener: this.priceListener,
            account: this.account,
            isLive: this.isLive
        });
        let allowance  = await this.client.init();
        if ((new BigNumber(allowance.toString()).lte(0))) {
            await this.client.approve(new BigNumber(1).shiftedBy(9).toString());
        }
        console.log("allowance", allowance);
        this.eventID = await this.client.getEventID('Daily_Music_Quiz_2', 3155760000, '0x02D99535E1BEFb3B31c37def6A87767773763aCF');
        console.log('Daily Music Quiz', this.eventID);
        const selectedAccount = await this.client.getCurrentAccount();
        this.isGameTokenLocked = await this.client.nonBiconomyContract.gameTokensLockedForPlayer(this.eventID, selectedAccount);
        console.log('Daily Music Quiz', this.isGameTokenLocked);
        if (this.isGameTokenLocked) this.hintsRevealed[0] = true;
        if (this.$route.query && this.$route.query.cn){
          this.contestNumber = this.$route.query.cn
        }
        else {
          const jsonURL = "/contestNumber.json" + "?cb=" + new Date().getTime();
          const response = await fetch(jsonURL);
          console.log("jsonURL", jsonURL);
          const contestNumberData = await response.json();
          console.log("contestNumberData", contestNumberData);
          this.contestNumber = contestNumberData.contestNumber;
        }
        const dataUrl = "/contest-" + this.contestNumber + "/contest.json" + "?cb=" + new Date().getTime(); 
        console.log("dataUrl", dataUrl);
        const dataResponse = await fetch(dataUrl);  
        console.log("dataResponse", dataResponse);
        const data = await dataResponse.json();
        console.log("data", data);
        this.clip = data;
        for (let i=0; i<data.hints.length; i++){
          this.clip.hints[i].text = data.hints[i].text; 
        }
        if (this.contestNumber == localStorage.getItem("LAST_CONTEST")){
            this.whatsAppLink = this.getShareMessage();
            this.overlayShow = true;
            this.latestPlayed = true;
        }
        const songList = "/answerSet.json" + "?cb=" + new Date().getTime();
        const songResponse = await fetch(songList);
        const songData = await songResponse.json();
        const masterSongList = songData.sort().map(s => s.toUpperCase());
        //this.songList = songData.sort().map(s => s.toUpperCase());

        let newList = [];
        for (const song of masterSongList) if (song != this.clip.answer.toUpperCase()) newList.push(song);
        
        const randomInts = Array.from(this.myRandomInts(9, newList.length - 1));
        for (let i=0; i<randomInts.length; i++){
          this.songList.push(newList[randomInts[i]]);
        }
        this.songList.push(this.clip.answer.toUpperCase());
        this.songList.sort();//.map(s => s.toUpperCase());
      

        let hintsRevealedForContest = JSON.parse(localStorage.getItem("HINTSREVEALED"));
        let indexRevealed = undefined;
        if (hintsRevealedForContest == undefined){
          hintsRevealedForContest = {};
          hintsRevealedForContest[this.contestNumber] = 0;
          localStorage.setItem("HINTSREVEALED", JSON.stringify(hintsRevealedForContest));
        }
        else {
          indexRevealed = hintsRevealedForContest[this.contestNumber];
          if (indexRevealed == undefined) hintsRevealedForContest[this.contestNumber] = 0;
        }
        for (let i=0; i<=indexRevealed; i++){
          this.hintsRevealed[i] = true;
        } 
        hintsRevealedForContest = JSON.parse(localStorage.getItem("HINTSREVEALED"));
        this.hintsRevealed[hintsRevealedForContest[this.contestNumber]] = true;
    },
    data: function() {
      return {
        isEditing: null,
        baseDate: '2022-02-15',
        clip: undefined,
        answer: '',
        offset: 0,
        hintsRevealed: {0: false, 1: false, 2: false},
        contestNumber: 1,
        shareText: "",
        gold: '🥇',
        silver: '🥈',
        bronze: '🥉',
        participation: '🏅',
        whatsAppUrl: 'whatsapp://send?',
        whatsAppText: 'text=SargamADay ',
        whatsAppLink: '',
        overlayAbsolute: true,
        overlayShow : false,
        overlayZIndex: 0,
        GOLD: 0,
        SILVER: 1,
        BRONZE: 2,
        PARTICIPATION: 3,
        display: '',
        winMessage: '',
        latestPlayed: false,
        nextContestMessage: 'Next Sargam Tomorrow !',
        songList: [],
        client: {},
        eventID: '',
        isGameTokenLocked: false,
        snack: {
            display: false,
            duration: 4000,
            color: "info",
            text: "",
        },

      }
    },
    computed: {
    ...mapGetters([
        "network",
        "getSelectedInstrument",
        "account",
        "loading",
        "balance",
        "durations",
        "selectedDuration",
        "isLive"
    ]),
    swiper() {
      return this.$refs.swiperSlider?.$swiper
    },
  },
  watch: {
    async network(oldValue, newValue) {
      if((!oldValue && newValue) || (oldValue.name !== newValue.name)) {
        try {
          this.startLoading()
          try {
            this.client.destroy();
          } catch (e) {
            console.log(e);
          }
          if (!this.network) return;
          await this.init();
          await this.fetch();
          let idx = this.items.findIndex(v => v.status === "Open")
          this.swiper?.slideTo(idx);
        } catch (e) {
          //this.setErrorMessage(e);
          console.error(e);
        } finally {
          this.stopLoading();
        }
      }
    },
  },
  async account(oldValue, newValue) {
      if(oldValue !== newValue) {
        try {
          this.startLoading()
          try {
            this.client.destroy();
          } catch (e) {
            console.log(e);
          }
          this.startTimer();
          if (!this.network) return;
          //await this.init();
          await this.fetch();
          let idx = this.items.findIndex(v => v.status === "Open")
          this.swiper?.slideTo(idx);
        } catch (e) {
          //this.setErrorMessage(e);
          console.error(e);
        } finally {
          this.stopLoading();
        }
      }
    },
    methods: {
    share: function(){
        let testingCodeToCopy = document.querySelector('#testing-code')
        testingCodeToCopy.setAttribute('type', 'text')
        testingCodeToCopy.value = this.silver
        testingCodeToCopy.select()
        document.execCommand('copy');
        testingCodeToCopy.setAttribute('type', 'hidden')
    },
    getEmbedUrl : function(){
      return this.clip.original + "?cb=" + new Date();
    },
    myRandomInts: function (quantity, max){
      const set = new Set()
      while(set.size < quantity) {
        set.add(Math.floor(Math.random() * max))
      }
      return set
    },
    checkLockStatus: async function(){
        const selectedAccount = await this.client.getCurrentAccount();
        this.isGameTokenLocked = await this.client.nonBiconomyContract.gameTokensLockedForPlayer(this.eventID, selectedAccount);
        //console.log('Daily Music Quiz', this.isGameTokenLocked);
        const msg = this.isGameTokenLocked ? 'Delphi Locked in Game Contract, You can Play' : 'Delphi Not Locked yet in game contract';
        const color = this.isGameTokenLocked ? 'success' : 'error';
        this.snack.display = true;
        this.snack.text = msg;
        this.snack.color = color;

        if (this.isGameTokenLocked) this.hintsRevealed[0] = true;
    },
    deposit: async function(){
        const withSigner = this.client.signerContract.connect(this.client.metamaskProvider.getSigner())
        await withSigner.lockGameTokens(this.eventID);
    },
    setHintsRevealed: function(index){
      this.hintsRevealed[index]=true;
      let hintsRevealedForContest = JSON.parse(localStorage.getItem("HINTSREVEALED"));
      if (index > hintsRevealedForContest[this.contestNumber]) {
        hintsRevealedForContest[this.contestNumber] = index;
        localStorage.setItem("HINTSREVEALED", JSON.stringify(hintsRevealedForContest));
      }
    },
    getClipURL: function(index){
      return "/contest-" + this.contestNumber + this.clip.hints[index].audio + "?cb=" + new Date().getTime();
    },
    getImgURL: function(index){
        console.log("/contest-" + this.contestNumber + this.clip.hints[index].img + "?cb=" + new Date().getTime());
      return "/contest-" + this.contestNumber + this.clip.hints[index].img + "?cb=" + new Date().getTime();
    },
    phoneShare: function(){
      console.log(navigator);
      navigator
      .share({
        text: this.whatsAppLink,  
        url: window.location.href
      })
    .then(() => alert('Successful share! 🎉'))
    .catch(err => alert('Share Failed'));
    },
    getHintText: function(idx){
      if (idx == 0) return "Show Gold Hint";
      if (idx == 1) return "Show Silver Hint";
      if (idx == 2) return "Show Bronze Hint";
    },
    //#d8bc92
    getHintColor: function(idx){
      if (idx == 0) return "#ffd700";
      if (idx == 1) return "#D3D3D3";
      if (idx == 2) return "#d8bc92";
    },
    getClipContributor: function(){
      if (this.clip == undefined) return "";
      return this.clip.contributor;
    },          
    playSound: function () {
      const sound = this.clip.url; 
      if(sound) {
        var audio = new Audio(sound);
        audio.play();
      }
    },
    getShareMessage: function(){
        const line_separator = '\r\n';
        let displayStr = '';
        let medalWon = localStorage.getItem("LAST_MEDAL")*1;
        let medalTally = JSON.parse(localStorage.getItem("MEDAL_TALLY"));
        this.display = 'SargamADay Quiz ';// + this.contestNumber;//this.whatsAppText;
        this.display += line_separator;
        if (medalWon == this.GOLD){
            this.winMessage = 'You Won Gold Medal 🥇';
        }
        else if (medalWon == this.SILVER){
            this.winMessage = 'You Won Silver Medal 🥈';
        }
        else if (medalWon == this.BRONZE){
            this.winMessage = 'You Won Bronze Medal 🥉';
        }
        else {
            this.winMessage = 'You Won Participation Medal 🏅';
        }
        
        this.display += this.winMessage;
        this.display += line_separator;
        this.display += 'Your Overall Medal Tally ';
        this.display += line_separator;
        this.display += '🥇 ' + medalTally["GOLD"];
        this.display += line_separator;
        this.display += '🥈 ' + medalTally["SILVER"];
        this.display += line_separator;
        this.display += '🥉 ' + medalTally["BRONZE"];
        this.display += line_separator;
        this.display += '🏅 ' + medalTally["PARTICIPATION"];
        this.display += line_separator;
        displayStr += this.display;
        return displayStr;
    },
    answerMatch: function(){
      const answerDecoded = utils.decode(this.clip.answer);
      return answerDecoded.toUpperCase() == this.answer.trim().toUpperCase();
    },
    evaluate: function(){
        if (this.latestPlayed) return;
        this.answer = this.answer.trim();
        const answerMatch = this.answerMatch();
        localStorage.setItem("LAST_CONTEST", this.contestNumber);
        this.latestPlayed = true;
        let medalTallyJSONString = localStorage.getItem("MEDAL_TALLY");
        let medalTally;
        if (!medalTallyJSONString) {
            medalTally = {"GOLD": 0, "SILVER": 0, "BRONZE": 0, "PARTICIPATION": 0};
            localStorage.setItem("MEDAL_TALLY", JSON.stringify(medalTally));
        }
        else {
            medalTally = JSON.parse(medalTallyJSONString);
        }
        if (answerMatch == true){
            if (this.hintsRevealed[2] == true){
                medalTally["BRONZE"] = medalTally["BRONZE"]*1 + 1;
                localStorage.setItem("LAST_MEDAL", this.BRONZE);
                
            }
            else if (this.hintsRevealed[1] == true){
                medalTally["SILVER"] = medalTally["SILVER"]*1 + 1;
                localStorage.setItem("LAST_MEDAL", this.SILVER);        
            }
            else {
                medalTally["GOLD"] = medalTally["GOLD"]*1 + 1;
                localStorage.setItem("LAST_MEDAL", this.GOLD);
            }
        }
        else {
            medalTally["PARTICIPATION"] = medalTally["PARTICIPATION"]*1 + 1;
            localStorage.setItem("LAST_MEDAL", this.PARTICIPATION);

        }
        localStorage.setItem("MEDAL_TALLY", JSON.stringify(medalTally));
        this.whatsAppLink = this.getShareMessage();
        this.overlayShow = true;
    }
  }
}

</script>
